import React from "react";
import ReactDOM from "react-dom";
import travel_01 from "../assets/1.jpeg";
import travel_02 from "../assets/2.jpeg";
import travel_03 from "../assets/5.jpg";
import Carousel from "react-bootstrap/Carousel";

function CarouselHero() {
  return (
    <Carousel>
      <Carousel.Item interval={2000}>
        <img className="d-block w-100" src={travel_01} alt="First slide" />
        <Carousel.Caption>
          <h3>SASAGO SALON1</h3>
          <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item interval={2000}>
        <img className="d-block w-100" src={travel_02} alt="Second slide" />
        <Carousel.Caption>
          <h3>SALON 2 NOMBRE</h3>
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item interval={2000}>
        <img className="d-block w-100" src={travel_03} alt="Third slide" />
        <Carousel.Caption>
          <h3>SALON 3 NOMBRE</h3>
          <p>
            Praesent commodo cursus magna, vel scelerisque nisl consectetur.
          </p>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
  );
}

export default CarouselHero;
